$('[data-wp-text-slider]').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    fade: true,
    asNavFor: '[data-wp-icon-slider]'
});
$('[data-wp-icon-slider]').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    asNavFor: '[data-wp-text-slider]',
    prevArrow: $('[data-wp-arrow-prev]'),
    nextArrow: $('[data-wp-arrow-next]'),
    dots: false,
    autoplay: true,
    focusOnSelect: true
});
